body {
  background: url(/build/images/drm.71328778.jpg) no-repeat center center fixed;
  background-size: cover;
}

.main-section {
  margin: 0 auto;
  margin-top: 130px;
  padding: 0;
}

.modal-content {
  background-color: #434e5a;
  opacity: .8;
  padding: 0 18px;
}

.user-img {
  margin-top: -60px;
  margin-bottom: 45px;
}

.user-img img {
  height: 120px;
  width: 120px;
}

.form-group {
  margin-bottom: 25px;
}

.form-group input {
  height: 42px;
  border-radius: 5px;
  border: 0;
  font-size: 18px;
  letter-spacing: 2px;
  padding-left: 54px;
}

.form-group button {
  width: 40%;
  margin: 5px 0 25px;
}

.form-group::before {
  font-family: FontAwesome;
  content: "\F007";
  position: absolute;
  font-size: 22px;
  left: 28px;
  padding-top: 4px;
  color: #555e60;
}

.form-group:last-of-type::before {
  content: "\F023";
}

.btn-login {
  background-color: #1c6288;
  font-size: 19px;
  border-radius: 5px;
  padding: 7px 14px;
  border: 1px solid #daf1ff;
}

.btn-login:hover {
  background-color: #13445e;
}

.forgot {
  padding: 5px 0 25px;
}

.forgot a {
  color: #daf1ff;
}

.error {
  color: #ffa700;
  font-size: large;
}
